<template>
    <!-- 全国疫情风险地区汇总 -->
    <div class="area">
        <div class="img"><img src="../../assets/images/area.png" alt="" width="100%"></div>
        <div class="content">
            <div class="th">全国风险地区汇总</div>            
            <div v-if="high.length > 0">
                <div class="title">
                    <img src="../../assets/images/high.png" alt="">
                    高风险区{{ high.length }}个
                </div>
                <ul class="list">
                    <li v-for="(item, index) in high" :key="index">
                    {{ item }}
                    </li>
                </ul>
            </div>
        
            <div v-if="mid.length > 0">
                    <div class="title">
                        <img src="../../assets/images/mid.png" alt="">
                        中风险区{{ mid.length }}个
                    </div>
                    <ul class="list">
                        <li v-for="(item, index) in mid" :key="index">
                        {{ item }}
                        </li>
                    </ul>
            </div>
        </div>
       
    </div>
</template>

<script>
import api from '../../api/index'
export default {
    name: 'Freewatch',

    data() {
        return {
            high:[], //高风险区数据
            mid: [], //中风险区域
        };
    },

    mounted() {
        
    },

    created() {
        api.getVirusInfo().then((res) => {
        // console.log(res.data.newslist[0]);
        let riskarea = res.data.newslist[0].riskarea;
        //解析
        this.high = riskarea.high;
        this.mid = riskarea.mid;
        });
    },
};
</script>

<style scoped>
.area .img{
    position: fixed;
}
.content{
    position: relative;
    top:2.6rem;
    background-color: white;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
    border-radius: 0.2rem 0.2rem 0 0;
    padding: 0.2rem;
    padding-top: 0.4rem;

}
.content .th{
    border-left:0.1rem solid rgb(65,105,226) ;
    padding-left: 0.1rem;
    margin-bottom: 0.1rem;
    font-size: 0.34rem;
}
.title{
    font-size: 0.3rem;
    margin-bottom: 0.3rem;      
    padding-top: 0.2rem;
}
.title img{
    width: 0.4rem;
    vertical-align: top;
}
.list li{
    margin-bottom: 0.2rem;
    color: #333;
    background:#f7f7f7;
    padding:0.1rem;
    border-radius: 0.1rem;
    letter-spacing: 0.01rem;
    line-height: 0.5rem;
}

</style>


